export default {
	data() {
		return {
			news: []
		}
	},
	onLoad() {
		this.getNewsList()
	},
	methods: {
		getNewsList() {
			this.$api.getNewsList()
			.then((res) => {	
				this.news = res.message
			})
		},
		goToDetail(id) {
			uni.navigateTo({
				url:'../detail/detail?id=' + id
			})
		}
	}
}
